@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@500&family=Roboto+Condensed:ital,wght@1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  font-family: 'Playfair Display', serif;
}
.App{
  
  display: flex;
  flex-direction: column;
  align-items: center;
  
  overflow-x: hidden;
 
}

.container{
  display: flex;
 justify-content: center;
  
 
}
.projBody{
  display: flex;
  flex-direction:column;
  width:300px;
  align-items: center;
 
  border-radius: 20px;
  
  
}
.sent{
  display: flex;
  flex-direction: column;
  width:300px;
  align-items: center;
 
  
}
.won{
  display: flex;
  flex-direction: column;
  align-items: center;
  
 
  width:300px;
 
}
.ongoing{
  display: flex;
  flex-direction: column;
  align-items: center;
  width:300px;
}

@media screen and (max-width:1072px) {
  h1.heading{
    display:block;
  }
  .container{
    display: flex;
    flex-direction:column;
  }

}
.text{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.container1{
    display: flex;
 justify-content: flex-start;
}

.list{
  
    border-radius: 20px;
}

.button{
    display: flex;
    font-size: 10px;
}

.container2{
    display:flex;
    flex-direction: row;
}


.slist{
    
    border-radius: 20px;
    width:150px ;
    max-width: 360px;
}

.button1{
    display: flex;
}


